import { Component, OnInit } from '@angular/core';

import { Subscription } from 'rxjs';
import { ButtonOptionsI } from '@shared/models/titlebar.model';
import { UserI } from '../users/models/users.model';
import { API_CONFIG, StatusEnum } from '@const/api.constant';
// import { SpinnerService } from '@shared/services/spinner.service';
import { ToastService } from '@shared/services/toast.service';
import { UsersHttpService } from '../users/services/users-http.service'
@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.scss']
})
export class UsersComponent implements OnInit {
  readonly button: ButtonOptionsI = {
    label: 'Add New User',
    routerLink: ['/users/add']
  };
  readonly pageTitle = 'Users';
 
  userCount:number = 10;
  usersList: UserI[] = [];

  subscriptions: Subscription = new Subscription();
  constructor(
    private _userHttp: UsersHttpService,
  ) {
   
  }
  
  ngOnInit() {
    this.getUserList({});
  }

  /**
   * @description get list of page category based on filter
   *
   * @param {number} { currentPage = 1 }
   * @memberof AclUserComponent
   */

  getUserList({ currentPage = 1 }) {
    // this._spinner.show('Fetching Users');
    this.subscriptions.add(
      this._userHttp
        .fetchAllUsers()
        .subscribe(
          (res) => {
            // this._spinner.hide();
            this.usersList = res;
            // this.fetchTotalUsersCount();
          },
          (error) => {
            console.log(error);
            
            // this._spinner.hide();
            // this._toast.error(error.error.message, 'Something went wrong');
          }
        )
    );
  }
  
}
