import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { UsersHttpService } from '../../services/users-http.service'
import get from 'lodash.get';
import set from 'lodash.set';
@Component({
  selector: 'app-users-form',
  templateUrl: './users-form.component.html',
  styleUrls: ['./users-form.component.scss']
})
export class UsersFormComponent implements OnInit {
  @Input() userWrapper;
  @Input() countryList;
  @Input() rolesList: string[];
  @Input() vendorCodesList: string[];

  @Output() submit: EventEmitter<void> = new EventEmitter();
  @Output() back: EventEmitter<void> = new EventEmitter();

  userForm: FormGroup;
  isMobileNumberAvailable:boolean = false;
  private subscriptions = new Subscription();

  constructor(private _formBuilder: FormBuilder, private _userHttp: UsersHttpService,) {}
  

  ngOnInit(): void {
    this.initEndpointForm()
  }
  /**
     * @description initiate endpoint form
     * @memberof EndpointFormComponent
     */
  initEndpointForm() {
    this.userForm = this._formBuilder.group({
    
      name: [get(this.userWrapper, ['name'], ''), Validators.required],
      endpoint: [
        get(this.userWrapper, ['endpoint'], ''),
        Validators.required
      ],
      method: [get(this.userWrapper, ['method'], 'GET')]
    });
  }
  /**
   * @description on submit, map to wrapper and emit submit event
   * @memberof EndpointFormComponent
   */
   onSubmit() {
    this.mapToWrapper();
    this.submit.emit();
  }

  /**
   * @description map form value to wrapper
   * @memberof EndpointFormComponent
   */
  mapToWrapper() {
    let formValue = this.userForm.getRawValue();
    set(this.userWrapper, ['name'], formValue['name']);
    set(this.userWrapper, ['endpoint'], formValue['endpoint']);
    set(this.userWrapper, ['method'], formValue['method']);
    
  }
}
